@import './vendor/variables.scss';

*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: auto;
  font-family: var(--main-font);
  padding-inline: 20px;
  padding-block: 10%;
  inline-size: clamp(300px, 100vw, 1000px);
  min-block-size: 100vh;
  background: var(--accent-color);
  display: flex;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;
}
